import React, { useState } from 'react';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import s from './Login.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../../redux/reducers/auth-reducer';
import { Navigate } from 'react-router-dom';
import { AppDispatch, RootStateType } from '../../redux/store/redux-store';

const Login = () => {
    const dispatch = useDispatch<AppDispatch>();
    const isAuth = useSelector((state: RootStateType) => state.auth.isAuth);
    const captchaUrl = useSelector((state: RootStateType) => state.auth.captchaUrl);

    const [loginError, setError] = useState<string | null>(null);

    const initialValues = {
        email: '',
        password: '',
        rememberMe: false,
        captcha: ''
    };
    
    type LoginFormType = typeof initialValues;

    const validate = (values: LoginFormType) => {
        const errors: Partial<LoginFormType> = { };

        if (!values.email) {
            errors.email = 'Required';
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'Invalid email address';
        }

        if (!values.password) {
            errors.password = 'Required';
        }

        return errors;
    };

    const onSubmit = (data: LoginFormType) => {
        setError(null);
        dispatch(login(data, (errorMessage: string | null) => setError(errorMessage)));
    };

    if (isAuth) {
        return <Navigate to='/profile' />
    }

    return (
        <div>
            <h1>Login</h1>
            <Formik
                initialValues={initialValues}
                validate={validate}
                onSubmit={onSubmit}
            >
                {({ errors, touched }) => (
                    <Form className={s.loginForm}>
                        <div className={s.loginFormContainer}>
                            <div className={s.inputContainer}>
                                <label>Email:</label>
                                <Field type="email" name="email" className={`${s.inputField} ${errors.email && touched.email ? s.errorBorder : ''}`} />
                                <ErrorMessage name="email" component="div" className={s.error} />
                            </div>
                            <div className={s.inputContainer}>
                                <label>Password:</label>
                                <Field type="password" name="password" className={`${s.inputField} ${errors.password && touched.password ? s.errorBorder : ''}`} />
                                <ErrorMessage name="password" component="div" className={s.error} />
                            </div>
                            <div className={s.checkBoxContainer}>
                                <label>Remember me</label>
                                <Field type="checkbox" name="rememberMe" className={s.checkbox} />
                            </div>
                            {captchaUrl && <div className={s.captcha}><img src={captchaUrl} alt='' /></div>}
                            {captchaUrl && <div className={s.inputContainer}>
                                <Field type="input" name="captcha" className={`${s.inputField} ${errors.captcha && touched.captcha ? s.errorBorder : ''}`} />
                                <ErrorMessage name="captcha" component="div" className={s.error} />
                            </div>
                            }
                            <div>
                                {loginError && <div className={s.serverError}>{loginError}</div>}
                            </div>
                            <button type="submit" className={s.button}>Login</button>
                        </div>
                    </Form>
                )}
            </Formik>
        </div>
    );
};

export default Login;